<template>
<div>
	<h2 class="intro-title">Message 消息提示</h2>
	<p class="intro-content">常用于主动操作后的反馈提示。与 Notification 的区别是后者更多用于系统级通知的被动提醒。</p>
	<h3 class="base-title">基础用法</h3>
	<p class="base-content">从顶部出现，3 秒后自动消失。</p>
	<div class="demo-block" :class="{'show-case-core':caseObj1.showCoreFlag}">
		<div class="demo-block-source">
			<bh-button type='primary' @click='open1()'>主要按钮</bh-button>
			<bh-button type='success' @click='open2()'>成功按钮</bh-button>
		</div>
		<div class="demo-block-core">
			<textarea class="demo-core-box" readonly="readonly">
				<bh-message type='success' message='恭喜你，这是一条成功消息'></bh-message>
			</textarea>
		</div>
		<div class="demo-block-footer">
			<div class="demo-ms-arr" @click='toggleDemoCore(caseObj1)'>
				<span class="demo-ms-s">显示代码</span>
				<span class="demo-ms-h">隐藏代码</span>
			</div>
			<a :href="caseObj1.coreUrl" target="_blank" class="demo-online" title="前往 codepen.io 运行此示例">在线运行</a>
		</div>
	</div>
	<h3 class="base-title">Attributes</h3>
	<table class="attri-table">
		<thead>
			<tr>
				<th>参数</th>
				<th>说明</th>
				<th>类型</th>
				<th>可选值</th>
				<th>默认值</th>
			</tr>
		</thead>
		<tbody>
			<tr>
				<td>message</td>
				<td>消息文字</td>
				<td>string / VNode</td>
				<td>—</td>
				<td>—</td>
			</tr>
			<tr>
				<td>type</td>
				<td>主题</td>
				<td>string</td>
				<td>success/warning/info/error</td>
				<td>info</td>
			</tr>
		</tbody>
	</table>
</div>
</template>

<script>
export default {
	data () {
		return {
			caseObj1: {
				showCoreFlag: false,
				coreUrl: 'javascript:void(0)',
			},
			caseObj2: {
				showCoreFlag: false,
				coreUrl: 'javascript:void(0)',
			},
		};
	},
	methods: {
		// 切换代码显隐
		toggleDemoCore (item) {
			item.showCoreFlag = !item.showCoreFlag;
		},
		open1() {
			this.$message('这是一条消息提示');
		},
		open2() {
			this.$message({
				message: '恭喜你，这是一条成功消息',
				type: 'success'
			});
		},
	},
}
</script>
